.App {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.centered {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

.dropzone {
  border: 2px dashed #0087F7;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 20px;
  width: 400px;
  min-height: 100px;
  background: #f5f5f5;
  color: #0087F7;
  cursor: pointer;
}

.filters {
  position: sticky;
  top: 0;
  background: #fff;
  z-index: 1;
  padding: 10px;
}
